* {
  font-family: Arial, Helvetica, sans-serif;
}
.back-img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  display: block;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-filter: opacity(.2);
  -moz-filter: opacity(.2);
  -o-filter: opacity(.2);
  -ms-filter: opacity(.2);
  filter: opacity(.2);
}
.sentiment-box {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
}
.terms-box span {
  font-size: 14pt;
  font-weight: bold;
  transform: rotate(-45deg);
}
.terms-box {
  display: flex;
  flex: 1;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  transition: background-color 1s ease-in-out;
}
.title-box {
  display: flex;
  flex: 1;
  width: 100%;
  justify-content: center;
  align-items: center;
  transition: background-color 1s ease-in-out;
}
.title {
  display: flex;
  flex: 20;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 48pt;
}
.close-btn {
  margin: 1rem;
  font-size: 14pt;
  position: absolute;
  top: 0;
  right: 0;
  background-color: grey;
  border-radius: 5px;
  color: white;
  box-shadow: 1px 1px 1px black;
  border: 1px solid grey;
  cursor: pointer;
}
.score {
  display: flex;
  flex: 1;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.score-p {
  font-size: 20pt;
  text-align: center;
  width: 100%;
  margin: 0;
  font-weight: bold;
}
.stars-p {
  display: flex;
  flex: 2;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.star-on {
  color: gold;
  font-size: 56pt;
  animation: growShrink 2s 1;
  transition: transform 1s ease-in-out;
}
.star-on:hover {
  transform: scale(1.5);
}
@keyframes growShrink {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(3);
  }
}
.star-off {
  color: grey;
  font-size: 56pt;
  transition: transform 1s ease-in-out;
}
.star-off:hover {
  transform: scale(1.5);
}
.btn-box {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.ok-btn {
  background-color: rgb(0, 0, 255);
  margin-top: 1rem;
  color: white;
  padding: 1rem;
  border-radius: 12px;
  cursor: pointer;
  font-size: 12pt;
  width: 150px;
  font-weight: bold;
}
.ok-btn:hover {
  background-color: rgb(0, 0, 121);
}

.bold-sp {
  font-weight:bolder;
  text-decoration: underline;
}
