.loader-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: stretch;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  z-index: 9999;
}
.loader {
  position: fixed;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #107723;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  animation: spin 2s linear infinite;
}
.p-load {
  position: fixed;
  text-align: center;
  color: white;
  font-weight: bold;
  margin-top: 40px;
  width: 50%;
  text-wrap: wrap;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
