.switch {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 1rem;
}
.fix-h {
  line-height: 110%;
}
.mod {
  display: flex;
  flex: 1;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease-in-out;
  border-radius: 12px;
  cursor: pointer;
}
.mod:hover {
  background-color: #a9a9a985 !important;
}
.mod-title {
  font-size: 14pt;
  font-weight: bold;
  text-wrap: wrap;
}
.news-textarea {
  font-family: "Roboto", sans-serif;
  display: block;
  margin: auto;
  resize: none;
  height: 300px;
  padding: 1rem;
  white-space: pre-line;
  text-wrap: wrap;
  border-radius: 12px;
  border: 1px solid gray;
  box-shadow: 1px 1px 1px grey;
  cursor: text;
}
@media only screen and (max-device-width: 500px) {
  .news-textarea,
  .countChars {
    width: 95%;
  }
}
@media only screen and (min-device-width: 501px) and (max-device-width: 800px) {
  .news-textarea,
  .countChars {
    width: 90%;
  }
}
@media only screen and (min-device-width: 801px) {
  .news-textarea,
  .countChars {
    width: 70%;
  }
}
.countChars {
  display: block;
  text-align: right;
  margin: auto;
  font-weight: bold;
}
.btnBox {
  margin-top: 1rem;
  margin-bottom: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.btn {
  color: white;
  padding: 1rem;
  border-radius: 12px;
  cursor: pointer;
  font-size: 12pt;
}
.red {
  background-color: rgb(200, 0, 0);
  transition: background-color 0.5s ease-in-out;
}
.red:hover {
  background-color: rgb(138, 2, 2);
}
.green {
  background-color: rgb(22, 205, 22);
  transition: background-color 0.5s ease-in-out;
}
.green:hover {
  background-color: #195128;
}
.tooltip .tooltiptext {
  margin-left: auto;
  margin-right: auto;
  visibility: hidden;
  background-color: white;
  width: 15rem;
  font-size: 12pt;
  text-wrap: wrap;
  overflow-wrap: break-word;
  padding: 1rem;
  text-align: justify;
  border-radius: 12px;
  position: absolute;
  z-index: 9999;
  border: 1px solid grey;
  box-shadow: 1px 1px 1px grey;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}
.progress-bars-container {
  padding: 8px;
  height: 140px;
}