* {
  font-family: Arial, Helvetica, sans-serif;
}
#description {
  user-select: text;
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  -o-user-select: text;
}
.summarization-box-sv {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  margin: auto;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
}
.title-box {
  display: flex;
  flex: 1;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.title {
  display: flex;
  flex: 20;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 44pt;
}
.close-btn-sv {
  margin: 1rem;
  font-size: 14pt;
  position: absolute;
  top: 0;
  right: 0;
  background-color: grey;
  border-radius: 5px;
  color: white;
  box-shadow: 1px 1px 1px black;
  border: 1px solid grey;
  cursor: pointer;
}
.close-btn-sv:hover {
  background-color: rgb(77, 76, 76);
}
.description-box {
  display: flex;
  flex-direction: column;
  flex: 4;
  align-items: center;
  justify-items: center;
  width: 100%;
}
.description-box p {
  border: 1px solid grey;
  text-align: justify;
  width: 80%;
  height: 100%;
  font-size: 14pt;
  background-color: rgb(247, 243, 243);
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 1px 1px 1px black;
}
.description-box p::first-letter {
  margin-left: 50px;
}
.btn-box-summ {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}
.ok-btn-sv {
  background-color: rgb(0, 0, 255);
  margin-top: 1rem;
  color: white;
  padding: 1rem;
  border-radius: 12px;
  cursor: pointer;
  font-size: 12pt;
  width: 150px;
  font-weight: bold;
}
.ok-btn-sv:hover {
  background-color: rgb(0, 0, 121);
}
.copy-btn-sv {
  font-size: 42pt;
  margin-bottom: -1.1rem;
}
.copy-box {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.copy-box:hover .copy-btn-sv {
  font-size: 44pt;
}
.copy-txt {
  font-weight: bold;
  margin-top: 15px;
}
