.text-box {
  margin: auto;
  padding: 1rem;
}
.txt-a {
  color: black;
  text-decoration: none;
}
@media only screen and (max-device-width: 500px) {
  .text-box {
    width: 95%;
  }
}
@media only screen and (min-device-width: 501px) and (max-device-width: 800px) {
  .text-box {
    width: 90%;
  }
}
@media only screen and (min-device-width: 801px) {
  .text-box {
    width: 70%;
  }
}
.p-exp {
  text-align: justify;
  font-size: 14pt;
  line-height: 1.5;
}
.p-exp::first-letter {
  margin-left: 2rem;
}
.tit-h1 {
  line-height: 110%;
  margin-bottom: 1rem;
  transition: background-color 0.3s ease-in-out;
  padding: 1rem;
  border-radius: 12px;
  font-weight: bold;
}
.tit-h1:hover {
  background-color: #cacaca85;
}
