.br-top {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  color: #606060;
  background-color: #f1f1f1;
  text-align: center;
}
.br-el-1 {
  display: flex;
  padding: 0.4em;
  padding-right: 3em;
  flex: 1;
  justify-content: flex-end;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  font-size: 12pt;
  border-right: 0.2em solid #dfdfdf;
}
.br-el-2 {
  display: flex;
  flex: 2;
  padding: 0.4em;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
  font-size: 12pt;
}
.br-link {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 12pt;
  color: inherit;
  text-decoration: none;
  text-wrap: wrap;
}
.if-header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 1rem;
  background: linear-gradient(to bottom, #195128, #107723, #195128);
  color: white;
  padding-top: 3rem;
  padding-bottom: 3rem;
}
.title-a {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: inherit;
  text-decoration: none;
}
.if-logo {
  width: 5rem;
  height: 6rem;
}
.if-name {
  font-weight: bold;
  font-size: 32pt;
  text-wrap: wrap;
  line-height: 90%;
}
.top-title {
  font-size: 12pt;
  display: block;
  text-wrap: wrap;
}
.bottom-title {
  display: block;
  font-size: 14pt;
  text-wrap: wrap;
}
